        width: 100%;
    }
    
    .Section4PrivateTextRegionLeft h2{
        margin-bottom: 17px;
        margin-right: 20px;
    }
    .Section4PrivateTextRegionLeft hr{
        border: 1.5px solid #000;
        width: 21%;
        margin-bottom: 32px;
    }
    .Section4PrivateCheckText{
        display: flex;
        align-items: start;
        justify-content: left;
        margin-bottom: 30px;
    }
    .Section4PrivateCheckText div{
        margin-right: 20px;
    }
    .Section4PrivateCheckText p{
        width: 100%;
    }
    .Section4PrivateTextRegionRight{
        width: 100%;
        margin-left: 0px;
    }
    
    .Section4PrivateTextRegionRight h2{
        margin-bottom: 17px;
    }
    .Section4PrivateTextRegionRight hr{
        border: 1.5px solid #000;
        width: 21%;
        margin-bottom: 32px;
    }
    
    .Section4PrivateContact{
        display: flex;
        align-items: center;
        justify-content: center;
