/* Styles for Section Two */
.section-two {
  padding: 80px 0; /* Add padding as needed */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.section-two .section-content {
  width: 100%;
  display: flex;
  justify-content: center;
}

.sec-two-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}


.sec-two-subsection {
  flex: 0 0 calc(50% - 10px); /* Adjust width of subsections */
  display: flex;
  flex-direction: row;
  align-items: center;
}


.sec-two-subsection-content {
  display: flex;
  flex-direction: column;
  align-items: left;
  padding-top: 0px;
  padding-bottom: 0px;
  padding-left: 20px;
  padding-right: 40px;
  text-align: left;
  border-left: 1.5px solid #000000; /* Adjust color as needed */
}

.sec-two-subsection-content h2 {
  padding-top: 0px;
  padding-bottom: 0px;
  margin-top: 0px; /* Adjust margin as needed */
  margin-bottom: 20px; /* Adjust margin as needed */
}

.sec-two-subsection-content p {
  padding-top: 0px;
  padding-bottom: 0px;
}

@media (max-width: 840px) {
  .section-two {
    padding: 55px 0; /* Add padding as needed */
  }

  .sec-two-container {
    flex-direction: column;
  }

  .sec-two-subsection:first-child {
    margin-bottom: 60px;
  }

  .sec-two-subsection-content {
    padding-right: 0px;
  }
  
}
